export default {
	props:{
		title:{
			type:String,
			default:'自定义头部'
		},
		hasBgc:{   //有背景色
			type:Boolean,
			default:false
		},
		isBack:{   //是否有返回箭头
			type: Boolean,
			default:false
		},
		isHeight:{   //头部撑开高度
			type:Boolean,
			default:true
		},
		isNavBack:{   //固定返回前面的页面
			type:Boolean,
			default:true
		},
		backUrl:{    //返回到指定页面
			type:String,
			default:'index'
		},
		backNum:{   //返回几个页面
			type:Number,
			default:1
		}
	},
	data() {
		return {
			statusBarHeight:'',
			headHeight:''
		};
	},
	methods:{
		goBack(url){
			if(this.isNavBack){
				const that = this
				uni.navigateBack({
					delta:that.backNum
				})
			}else{
				var isUrl = `/pages/${url}/${url}`
				uni.redirectTo({
					url: isUrl
				})
			}
		}
	},
	created() {
		const system = uni.getSystemInfoSync()
		this.statusBarHeight = system.statusBarHeight || 25
		this.headHeight = (system.statusBarHeight || 25) + 45//可调整
	}
}
